:root {
  --background: #f8fafc;
  --foreground: #111827;
}

html, body {
  height: 100%;
  overscroll-behavior: contain;
}

body {
  background-color: var(--background);
  color: var(--foreground);
  overflow: hidden;
}

/* Custom Scrollbar for the editor */
.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: transparent;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #cbd5e1;
  border-radius: 4px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background-color: #94a3b8;
}

/* Editor Specific Styles */
.ProseMirror:focus {
  outline: none;
}

.editor-content {
  min-height: 500px;
  outline: none;
}

/* Added paragraph spacing */
.editor-content p {
  margin-bottom: 1.5em;
  line-height: 1.8;
}

.editor-content blockquote {
  border-left: 4px solid #3b82f6;
  padding-left: 1rem;
  margin-left: 0;
  font-style: italic;
  color: #4b5563;
  margin-bottom: 1.5em;
}

.editor-content ul {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin-bottom: 1.5em;
}

.editor-content ol {
  list-style-type: decimal;
  padding-left: 1.5rem;
  margin-bottom: 1.5em;
}

.editor-content h2 {
  font-size: 1.5em;
  font-weight: 700;
  margin-top: 1.5em;
  margin-bottom: 0.75em;
  color: #111827;
}

.editor-content h3 {
  font-size: 1.25em;
  font-weight: 600;
  margin-top: 1.25em;
  margin-bottom: 0.75em;
  color: #1f2937;
}

.editor-content a {
  color: #2563eb;
  text-decoration: underline;
}

/* Ask AI target highlight */
.editor-content mark.ask-ai-highlight {
  background: #fef3c7;
  color: inherit;
  border-radius: 4px;
  box-shadow: inset 0 -1px 0 #f59e0b;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 0 2px;
  position: relative;
}

.editor-content mark.ask-ai-highlight::after {
  content: 'Ask AI';
  font-size: 10px;
  line-height: 1.2;
  color: #b45309;
  background: #fef08a;
  border: 1px solid #f59e0b;
  border-radius: 9999px;
  padding: 2px 6px;
  text-transform: uppercase;
  letter-spacing: 0.02em;
}

/* Table Styling */
.editor-content table {
  border-collapse: collapse;
  width: 100%;
  margin: 1.5em 0;
  font-size: 0.95em;
}

.editor-content th {
  background-color: #f8fafc;
  font-weight: 600;
  text-align: left;
  padding: 0.75rem;
  border: 1px solid #e2e8f0;
  color: #334155;
}

.editor-content td {
  padding: 0.75rem;
  border: 1px solid #e2e8f0;
  color: #475569;
}

.editor-content tr:nth-child(even) {
  background-color: #fcfcfc;
}

